                      </span>
                      <h5 class="card-title">
                        <a href="#" class="text-main">
                          {{ item.title }}
                        </a>
                      </h5>
                      <p class="card-text">
                        {{ item.content }}
                      </p>
                    </div>
                    <div class="card-footer d-flex bg-white border-0">
                      <div class="d-flex flex-column">
                        <!--  -->
                        <div class="h5" v-if="!item.price"> {{ item.origin_price }} </div>
                        <!--  -->
                        <!-- 0''nan  false -->
                        <del class="h6 text-secondary" v-if="item.price"> {{ item.origin_price }} </del>
                        <div class="h5 text-sub" v-if="item.price"> {{ item.price }} </div>
                      </div>
                      <button type="button" class="btn btn-outline-third ml-auto card-cart" @click="addtoCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
                        <i class="fas fa-shopping-cart" v-else></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="tab-pane fade" id="list-bouquet" role="tabpanel" aria-labelledby="list-bouquet-list">
              <div class="row mt-4">
                <div class="col-md-4 mb-4" v-for="item in sortProduct" :key="item.id">
                  <div class="card border-0 shadow-sm">
                    <a href="#" @click.prevent="getProductID(item.id)">
                      <div style="height: 300px; background-size: cover; background-position: center;"
                        :style="{backgroundImage: `url(${item.imageUrl})`}">
                      </div>
                    </a>
                    <div class="card-body">
                      <span class="badge badge-sub float-right ml-2 text-white">
                        {{ item.category }}
                      </span>
                      <h5 class="card-title">
                        <a href="#" class="text-main">
                          {{ item.title }}
                        </a>
                      </h5>
                      <p class="card-text">
                        {{ item.content }}
                      </p>
                    </div>
                    <div class="card-footer d-flex bg-white border-0">
                      <div class="d-flex flex-column">
                        <!--  -->
                        <div class="h5" v-if="!item.price"> {{ item.origin_price }} </div>
                        <!--  -->
