<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <header>
      <NavBar />
    </header>
    <div class="container py-3">
      <div class="row justify-content-center">
        <div class="col-md-10">
          <div class="row">
            <div class="col-md-6 text-center">
              <img :src="product.imageUrl" class="img-detail img-thumbnail" alt="">
            </div>
            <div class="col-md-6">
              <h2 class="text-main">{{ product.title }}</h2>
              <blockquote class="blockquote mt-3">
                <p class="mb-0">{{ product.content }}</p>
                <footer class="blockquote-footer text-right">{{ product.description }}</footer>
              </blockquote>
              <div class="d-flex justify-content-between align-items-baseline">
                <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
                <del class="h6 text-secondary" v-if="product.price"> {{ product.origin_price }} </del>
                <div class="h4 text-sub" v-if="product.price"> {{ product.price }} </div>
              </div>
              <select name="" class="form-control mt-3" v-model="product.num">
                <option :value="num" v-for="num in 10" :key="num">
                   {{num}} {{product.unit}}
                </option>
              </select>
              <button type="button" class="btn btn-outline-third mt-3 d-flex ml-auto align-items-center"
              @click="addtoCart(product.id, product.num)">
                <i class="fas fa-spinner fa-spin fa-fw" v-if="product.id === status.loadingItem"></i>
                <i class="fas fa-shopping-cart fa-fw" v-else></i>  </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <ShoppingCart :cart-data="cartItem" :loading-img="status" @opencart="getCart(1)" @removecart="removeCartItem"></ShoppingCart>
  </div>
</template>

<script>
  import NavBar from "../NavBar";
  import Footer from "../Footer";
  import ShoppingCart from '../ShoppingCart'
  import $ from "jquery";

  export default {
    data() {
      return {
        productId: '',
        product: {},
        // 
        cartItem: [],
        isLoading: false,
        status: {
          loadingItem: '',
        },
      }
    },
    components: {
      NavBar,
      Footer,
      ShoppingCart,
    },
    methods: {
      getProduct(id) {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
          //  1
          response.data.product.num = 1;
          vm.product = response.data.product;
          vm.isLoading = false;
        });
      },
      //     qty = 1 qty  1
      addtoCart(id, qty = 1) {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
        //  ID  loadingItem 
        vm.status.loadingItem = id;
        const cart = {
          product_id: id,
          //  qty 
          qty,
        };
        // {data: }
        this.$http.post(api, {
          data: cart
        }).then((response) => {
          // Modal  loadingItem 
          vm.status.loadingItem = '';
          // 
          vm.getCart();
        });
      },
      // 
      getCart(open) {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        if (open == 1) {
          $('.cart-dropdown').toggleClass('show');;
        }
        this.$http.get(api).then((response) => {
          vm.cartItem = response.data.data;
        });
      },
      //  dropmenu 
      toggleCart() {
        $(document).on('click', function (e) {  
          //  .cart-dropdown  .cart-dropdown  .show 
          //  .cart-dropdown  .show 
          if(!e.target.closest('.cart-dropdown') && $('.cart-dropdown').hasClass('show')){
            $('.cart-dropdown').removeClass('show');
          }       
          // if (e.target.id != 'cart-id') {
          //   $(".cart-menu").toggleClass('show');
          // }
        });
      },
      // 
      removeCartItem(id) {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
        vm.status.loadingItem = id;
        this.$http.delete(api).then((response) => {
          vm.status.loadingItem = '';
          // 
          vm.getCart();
        });
      },
    },
    created() {
      //  ID
      // orderId  index.js 
      this.productId = this.$route.params.productId;
      this.getProduct(this.productId);
      this.getCart();
    },
    mounted() {
      // 
      this.toggleCart();
    }
  }

</script>



